const html = '\
<div  id=\'write\'  class = \'\'><h1><a name="cookie-sessionstorage-localstorage" class="md-header-anchor"></a><span>cookie sessionStorage localStorage</span></h1><div class=\'md-toc\' mdtype=\'toc\'><p class="md-toc-content" role="list"><span role="listitem" class="md-toc-item md-toc-h1" data-ref="n0"><a class="md-toc-inner" href="#cookie-sessionstorage-localstorage">cookie sessionStorage localStorage</a></span><span role="listitem" class="md-toc-item md-toc-h2" data-ref="n4"><a class="md-toc-inner" href="#cookie">cookie</a></span><span role="listitem" class="md-toc-item md-toc-h2" data-ref="n13"><a class="md-toc-inner" href="#sessionstorage-localstorage">sessionStorage localStorage</a></span><span role="listitem" class="md-toc-item md-toc-h2" data-ref="n28"><a class="md-toc-inner" href="#三者区别">三者区别</a></span><span role="listitem" class="md-toc-item md-toc-h4" data-ref="n31"><a class="md-toc-inner" href="#传递">传递：</a></span><span role="listitem" class="md-toc-item md-toc-h4" data-ref="n37"><a class="md-toc-inner" href="#存储⼤⼩">存储⼤⼩：</a></span><span role="listitem" class="md-toc-item md-toc-h4" data-ref="n43"><a class="md-toc-inner" href="#有期时间">有期时间：</a></span><span role="listitem" class="md-toc-item md-toc-h4" data-ref="n51"><a class="md-toc-inner" href="#api-简易性">API 简易性</a></span></p></div><h2><a name="cookie" class="md-header-anchor"></a><span>cookie</span></h2><ul><li><span>使用 </span><code>document.cookie</code><span> 获取和改变</span></li></ul><blockquote><p><span>cookie 作用域</span></p><ol><li><span>省略domain参数，那么domain默认为当前域名。</span></li><li><span>domain参数可以设置父域名以及自身，但不能设置其它域名，包括子域名，否则cookie不起作用。</span></li><li><span>有效作用域：domain本身以及domain下的所有子域名。</span></li></ol></blockquote><h2><a name="sessionstorage-localstorage" class="md-header-anchor"></a><span>sessionStorage localStorage</span></h2><ul><li><span>设置数据：</span><code>setItem(key,value)</code></li><li><span>获取数据：</span><code>getItem(key)</code></li><li><span>删除数据：</span><code>removeItem(key)</code></li><li><span>清空所有数据：</span><code>clear()</code></li></ul><h2><a name="三者区别" class="md-header-anchor"></a><span>三者区别</span></h2><h4><a name="传递" class="md-header-anchor"></a><span>传递：</span></h4><ol><li><code>cookie</code><span>数据始终在同源的http请求中携带（即使不需要），即会在浏览器和服务器间来回传递</span></li><li><code>sessionStorage</code><span>和 </span><code>localStorage</code><span>不会⾃动把数据发给服务器，仅在本地保存</span></li></ol><h4><a name="存储⼤⼩" class="md-header-anchor"></a><span>存储⼤⼩：</span></h4><ol><li><code>cookie</code><span>数据⼤⼩不能超过</span><code>4k</code></li><li><code>sessionStorage</code><span>和 </span><code>localStorage</code><span>虽然也有存储⼤⼩的限制，但⽐ cookie ⼤得 多，可以达到</span><code>5M或更⼤</code></li></ol><h4><a name="有期时间" class="md-header-anchor"></a><span>有期时间：</span></h4><ol><li><code>localStorage</code><span>存储持久数据，浏览器关闭后数据不丢失除⾮主动删除数据 </span></li><li><code>sessionStorage</code><span>数据在当前浏览器窗⼝关闭后⾃动删除 </span></li><li><code>cookie</code><span>设置的 cookie 过期时间之前⼀直有效，即使窗⼝或浏览器关闭</span></li></ol><h4><a name="api-简易性" class="md-header-anchor"></a><span>API 简易性</span></h4><ol><li><code>cookie</code><span>需要自己封装</span></li><li><code>sessionStorage</code><span>和 </span><code>localStorage</code><span>都有现成的</span><code>API</code></li></ol></div>\
'; export default html