import React, { useState } from 'react'
import './Tab.scss'
import { withRouter } from 'react-router-dom'

const Tab = ({ history, tabs, isPC }) => {
    const [ activeTab, setActiveTab ] = useState(sessionStorage.getItem('tab') || tabs[0])
    const [ activePhoneList, setActivePhoneList] = useState(false)

    const tabClick = (tab) => {
        history.push(`/blog/catalog/${tab}`)
        setActiveTab(tab)
        sessionStorage.setItem('tab', tab)
    }

    const phoneListClick = () => { setActivePhoneList(!activePhoneList) }

    return (
        isPC ? 
            <ul className="tab-container">
                { tabs.map(tab => (
                    <li 
                        className={`tab-item ${(activeTab === tab) && 'active'}`} 
                        key={tab}
                        onClick={() => { tabClick(tab) }}
                    >
                        { tab }
                    </li>
                ))} 
            </ul> 
        :
            <div className="tab-container-phone" onClick={phoneListClick}>
                <svg t="1607413597772" className={`icon ${activePhoneList && 'list-active'}`} fill={activePhoneList ? '#0baaa3' : '#e4e4e4'} viewBox="0 0 1024 1024" version="1.1" xmlns="http://www.w3.org/2000/svg" p-id="1674" width="30" height="30"><path d="M256 232V152c0-17.674 14.326-32 32-32h704c17.674 0 32 14.326 32 32v80c0 17.674-14.326 32-32 32H288c-17.674 0-32-14.326-32-32z m32 352h704c17.674 0 32-14.326 32-32v-80c0-17.674-14.326-32-32-32H288c-17.674 0-32 14.326-32 32v80c0 17.674 14.326 32 32 32z m0 320h704c17.674 0 32-14.326 32-32v-80c0-17.674-14.326-32-32-32H288c-17.674 0-32 14.326-32 32v80c0 17.674 14.326 32 32 32zM32 288h128c17.674 0 32-14.326 32-32V128c0-17.674-14.326-32-32-32H32C14.326 96 0 110.326 0 128v128c0 17.674 14.326 32 32 32z m0 320h128c17.674 0 32-14.326 32-32v-128c0-17.674-14.326-32-32-32H32c-17.674 0-32 14.326-32 32v128c0 17.674 14.326 32 32 32z m0 320h128c17.674 0 32-14.326 32-32v-128c0-17.674-14.326-32-32-32H32c-17.674 0-32 14.326-32 32v128c0 17.674 14.326 32 32 32z" p-id="1675"></path></svg>
                {
                    activePhoneList && tabs.length && 
                    <ul className="list-container-phone">
                        { tabs.map(tab => (
                            <li 
                                className={`tab-item ${(activeTab === tab) && 'active'}`} 
                                key={tab}
                                onClick={() => { tabClick(tab) }}
                            >
                                { tab }
                            </li>
                        ))}
                    </ul>
                }
            </div>
    )
}

export default withRouter(Tab)