import React from 'react'
import { withRouter, Link, Redirect } from 'react-router-dom'
import './Catalog.scss'
import '../config/style.css'

const Catalog = ({ match, jsonData }) => {
    const tab = match.params.tab
    const catalog = jsonData[tab]
    const catalogLabels = Reflect.ownKeys(catalog)

    if (tab === 'home') {
        return <Redirect to='/blog' />
    } else {
        return (
            <div className="catalog-container">
                <h1>{ tab }</h1>
                { catalogLabels.map(label => { // 分类标签
                    const catalogList = catalog[label]
                    return (
                        <div key={label}>
                            { /* 标签标题 */}
                            <h2> 
                                <span>{ label }</span>
                            </h2>
                            { /* 子目录 */}
                            <ul> 
                                { catalogList.map(catalogItem => (
                                    <li key={catalogItem.id}>
                                        <Link to={`/blog/article/${catalogItem.id}`}>
                                            <span className="title">{ catalogItem.title }</span>
                                        </Link>
                                    </li>
                                )) }
                            </ul>
                        </div>
                    )
                }) }
            </div>
        )
    }
}

export default withRouter(Catalog)