import React, { useState, useRef, useEffect } from 'react'
import miment from 'miment'
import { Link } from 'react-router-dom'
import './Search.scss'

function debounce (fn, delay) { // 防抖
       let timer = null
       return function (...arg) {
           const that = this 
           if (timer) {
               clearTimeout(timer) 
           }
           timer = setTimeout(function(){
               fn.apply(that, arg)
           }, delay)
       }
} 

// 组件
const Search = ({ jsonDataArr = [] }) => {
    const [ inputValue, setInputValue ] = useState('')
    const [ status, setStatus ] = useState(false)
    const [ showData, setShowData] = useState([])
    const inputRef = useRef(null)

    const iconClick = () => { 
        const newStatus = !status
        setStatus(newStatus)
        if (inputRef && newStatus) {
            inputRef.current.focus()
        } else {
            inputRef.current.blur()
            setShowData([])
            setInputValue('')
        }
    }  
    
    const handlerSearch = (value) => { // 搜索
        if (value) {
            const newShowData = jsonDataArr.filter(data => data.title.toLowerCase().includes(value))
            setShowData(newShowData)
        } else {
            setShowData([])
        }
    }

    const inputChange = (e) => { // 输入框值改变
        const value = e.target.value
        setInputValue(value)

        debounce(handlerSearch, 200)( value.toLowerCase() )
    }

    return (
        <div className="search-container">
            <input 
                type="text" 
                className={`search-input ${status && 'input-show'}`}
                ref={inputRef}
                value={inputValue} 
                onChange={inputChange}
            />
            <div className={`search-icon-container ${status && 'icon-show'}`} onClick={iconClick}>
                <img src={require('../config/search.png').default} alt="搜索"/>
            </div>
            { status && showData.length && 
                <ul className="show-container">
                    { showData.map(item => (
                        <li className="show-item" key={item.id}>
                            <Link to={`/blog/article/${item.id}`} onClick={iconClick}>
                                <div className="title">{ item.title }</div>
                                <div className="time">{ miment().distance(item.time).format('hh 时 mm 分前') }</div>
                                <div className="extra">{ item.extra }</div>
                            </Link>
                        </li>
                    )) }
                </ul>
            }
        </div>
    )
}

export default Search