const html = '\
<div  id=\'write\'  class = \'\'><h1><a name="tcp-和-udp" class="md-header-anchor"></a><span>TCP 和 UDP</span></h1><div class=\'md-toc\' mdtype=\'toc\'><p class="md-toc-content" role="list"><span role="listitem" class="md-toc-item md-toc-h1" data-ref="n0"><a class="md-toc-inner" href="#tcp-和-udp">TCP 和 UDP</a></span><span role="listitem" class="md-toc-item md-toc-h2" data-ref="n20"><a class="md-toc-inner" href="#区别">区别</a></span></p></div><h2><a name="区别" class="md-header-anchor"></a><span>区别</span></h2><ol><li><code>TCP</code><span>是面向连接的，</span><code>UDP</code><span>是无连接的即发送数据前不需要先建立链接。 </span></li><li><code>TCP</code><span>提供可靠的服务。即无差错，不丢失，不重复，且按序到达。</span><code>UDP</code><span>尽最大努力交付，即不保证可靠交付。 </span></li><li><code>TCP</code><span>是面向字节流，</span><code>UDP</code><span>面向报文，并且网络出现拥塞不会使得发送速率降低（因此会出现丢包）。 </span></li><li><code>TCP</code><span>只能是1对1的，</span><code>UDP</code><span>支持1对1、1对多。 </span></li><li><code>TCP</code><span>的首部较大为</span><code>20</code><span>字节，而</span><code>UDP</code><span>只有</span><code>8</code><span>字节。 </span></li></ol></div>\
'; export default html