const html = '\
<div  id=\'write\'  class = \'\'><h1><a name="输入url至页面显示的过程" class="md-header-anchor"></a><span>输入URL至页面显示的过程</span></h1><h2><a name="过程" class="md-header-anchor"></a><span>过程</span></h2><ol><li><p><span>在浏览器地址栏输⼊</span><strong><span>URL </span></strong></p></li><li><p><span>浏览器查看</span><mark><span>缓存</span></mark></p><ul><li><p><span>如果资源未缓存，发起新请求 </span></p></li><li><p><span>如果已缓存，检验是否⾜够新鲜，⾜够新鲜直接提供给客户端，否则与服务器进⾏验 证</span></p></li><li><p><span>检验新鲜通常有两个HTTP头进⾏控制 </span><mark><span>Expires</span></mark><span> 和 </span><mark><span>Cache-Control</span></mark><span> ：</span></p><ol><li><code>HTTP1.0</code><span>提供 </span><code>Expires</code><span>，值为⼀个绝对时间表示缓存新鲜⽇期 </span></li><li><code>HTTP1.1</code><span>增加了 </span><code>Cache-Control: max-age=</code><span> ,值为以秒为单位的最大新鲜时间</span></li></ol></li></ul></li><li><p><span>浏览器</span><mark><span>解析 URL</span></mark><span> 获取协议，域名，端⼝</span></p></li><li><p><span>浏览器</span><mark><span>组装⼀个HTTP（GET）请求报⽂</span></mark><span> </span></p></li><li><p><span>浏览器</span><mark><span>获取主机目标 </span><strong><span>ip</span></strong><span> 地址</span></mark></p><ul><li><span>浏览器缓存 </span></li><li><span>本机缓存</span></li><li><span>hosts⽂件 </span></li><li><span>路由器缓存 </span></li><li><span>ISP DNS缓存 </span></li><li><span>DNS递归查询</span></li></ul></li><li><p><span>打开⼀个</span><mark><span>socket</span></mark><span>与⽬标IP地址，端⼝建⽴</span><mark><span>TCP</span></mark><span>链接，</span><mark><span>三次握⼿</span></mark><span>如下： </span></p><ol><li><span>客户端发送⼀个TCP的</span><code>SYN=1</code><span>，</span><code>Seq=X</code><span>的包到服务器端⼝ </span></li><li><span>服务器发回</span><code>SYN=1</code><span>，</span><code>ACK=X+1</code><span>， </span><code>Seq=Y</code><span>的响应包 </span></li><li><span>客户端发送</span><code>ACK=Y+1</code><span>， </span><code>Seq=Z</code></li></ol></li><li><p><span>TCP链接建⽴后</span><mark><span>发送HTTP请求</span></mark><span> </span></p></li><li><p><span>服务器接受请求并解析，检查HTTP</span><mark><span>请求头</span></mark><span>是否包含</span><mark><span>缓存验证信息</span></mark><span>，如果验证缓存新鲜，返回</span><mark><span>304</span></mark><span>等对应状态码 </span></p></li><li><p><span>处理程序读取完整请求并</span><mark><span>准备HTTP响应</span></mark><span>，将响应报⽂通过TCP连接发送回浏览器 </span></p></li><li><p><span>浏览器接收HTTP响应，然后根据情况选择</span><mark><span>关闭TCP连接</span></mark><span>或者</span><mark><span>保留重⽤</span></mark><span>，关闭</span><mark><span>TCP连接的四次握⼿</span></mark><span>如下： </span></p><ol><li><span>客户端发送一个TCP的</span><code>FIN=1</code><span>，</span><code>Seq=U</code><span>的包到服务器端口</span></li><li><span>服务器发回</span><code>FIN=1</code><span>，</span><code>ACK=U+1</code><span>， </span><code>Seq=V</code><span>的响应包 ，此时处于半关闭状态，但可能服务器端还有一个数据要发送</span></li><li><span>服务器发送</span><code>FIN=1</code><span>，</span><code>ACK=U+1</code><span>， </span><code>Seq=W</code><span>的响应包 </span></li><li><span>客户端发送</span><code>ACK=W+1</code><span>， </span><code>Seq=U+1</code></li></ol></li><li><p><span>浏览器检查</span><mark><span>响应状态码</span></mark><span>：是否为1XX，3XX， 4XX， 5XX，这些情况处理与2XX不同 </span></p></li><li><p><span>如果资源</span><mark><span>可缓存</span></mark><span>，进⾏缓存 </span></p></li><li><p><span>对响应进⾏</span><mark><span>解码</span></mark><span>（例如gzip压缩)</span></p></li><li><p><span>根据</span><mark><span>资源类型</span></mark><span>决定如何处理</span></p></li><li><p><span>显示页面</span></p></li></ol><h4><a name="假设返回资源为-html-文档" class="md-header-anchor"></a><span>假设返回资源为 HTML 文档</span></h4><ol><li><p><mark><span>解析HTML⽂档，构件DOM树，下载资源，构造CSSOM树，执⾏js脚本</span></mark><span>，这些操作没有严格的先后顺序，以下分别解释 </span></p></li><li><p><mark><span>构建DOM树</span></mark><span>： </span></p><ol><li><span>把字节流解码成字符流</span></li><li><span>通过词法分析器解释成词语，构建成节点</span></li><li><span>把节点组建成 </span><code>DOM</code><span> 树</span></li></ol></li><li><p><span>解析过程中遇到图⽚、样式表、</span><code>js</code><span>⽂件，</span><mark><span>启动下载</span></mark></p></li><li><p><mark><span>构建CSSOM树</span></mark><span>： </span></p><ol><li><span>把字节流解码成字符流</span></li><li><span>通过词法分析器解释成词语，构建成</span><code>CSS</code><span>对象</span></li><li><span>把 </span><code>CSS</code><span> 对象组建成 </span><code>DOM</code><span> 树</span></li></ol></li><li><p><span>根据</span><code>DOM</code><span>树和</span><code>CSSOM</code><span>树</span><mark><span>构建渲染树</span></mark></p><ol><li><span>从</span><code>DOM</code><span>树的根节点遍历所有可⻅节点</span></li><li><span>对每⼀个可⻅节点，找到恰当的</span><code>CSSOM</code><span>规则并应⽤ </span></li><li><span>发布可视节点的内容和计算样式 </span></li></ol></li><li><p><span>js解析如下</span></p><ol><li><span>浏览器创建</span><code>Document对象</code><span>并解析HTML，将解析到的元素和⽂本节点添加到⽂档中，此 时</span><code>document.readystate为loading</code><span> </span></li><li><span>HTML解析器遇到</span><code>没有async和defer的script</code><span>时，将他们添加到⽂档中，然后执⾏⾏内 或外部脚本。这些脚本会同步执⾏，并且在脚本下载和执⾏时解析器会暂停。这样就可 以⽤document.write()把⽂本插⼊到输⼊流中。</span><strong><span>同步脚本经常简单定义函数和注册事件处理程序，他们可以遍历和操作script和他们之前的⽂档内容 </span></strong></li><li><span>当解析器遇到设置了</span><code>async</code><span>属性的script时，开始下载脚本并继续解析⽂档。脚本会在它 </span><code>下载完成后尽快执⾏</code><span>，但是解析器不会停下来等它下载。异步脚本禁⽌使⽤ document.write()，它们可以访问⾃⼰script和之前的⽂档元素 </span></li><li><span>当⽂档完成解析，</span><code>document.readState变成interactive</code></li><li><span>所有</span><code>defer脚本</code><span>会按照在⽂档出现的顺序执⾏，延迟脚本能访问完整⽂档树，禁⽌使⽤ </span><code>document.write()</code></li><li><span>浏览器在</span><code>Document</code><span>对象上触发</span><code>DOMContentLoaded</code><span>事件 </span></li><li><span>此时⽂档完全解析完成，浏览器可能还在等待如图⽚等内容加载，等这些</span><code>内容完成载⼊ 并且所有异步脚本完成载⼊和执⾏</code><span>，document.readState变为complete，window触发 load事件 </span></li></ol></li></ol></div>\
'; export default html