import React, { useState, useEffect } from 'react'

const Style = {
    position: 'fixed',
    bottom: '10%',
    left: 30,
    cursor: 'pointer'
}

const BackTop = ({ isPC }) => {
    const [ status, setStatus ] = useState(false)

    const iconClick = () => { window.scrollTo(0, 0); }

    useEffect(() => {
        const handlerScroll = () => {
            let scrollTop = window.pageYOffset || document.documentElement.scrollTop || document.body.scrollTop || 0
            if (scrollTop > 100) {
                setStatus(true)
            } else {
                setStatus(false)
            }
        }
        window.addEventListener('scroll', handlerScroll, true)
        return () => {
            window.removeEventListener('scroll', handlerScroll, true)
        }
    })

    return (
        isPC && status &&
        <svg style={{ ...Style }} onClick={iconClick} t="1607434938896" className="icon" viewBox="0 0 1024 1024" version="1.1" xmlns="http://www.w3.org/2000/svg" p-id="1657" width="30" height="30">
            <path d="M512 2.373117C230.785632 2.373117 2.966396 230.192352 2.966396 511.406721S230.785632 1020.440324 512 1020.440324s509.033604-227.819235 509.033604-509.033603S793.214368 2.373117 512 2.373117z m341.728853 619.383546L776.602549 698.882966a39.393743 39.393743 0 0 1-55.76825 0L512 490.048667 303.165701 698.882966a39.393743 39.393743 0 0 1-55.76825 0l-77.126304-77.126303a39.393743 39.393743 0 0 1 0-55.76825l234.938586-234.938587c1.186559-1.186559 1.186559-2.373117 2.373118-2.373117l77.126303-77.126303c3.559676-3.559676 8.30591-7.119351 13.052144-8.30591 14.238702-5.932793 32.03708-3.559676 42.716106 8.30591l77.126304 77.126303c1.186559 1.186559 3.559676 3.559676 4.746234 5.932793l232.565469 232.565469c14.238702 14.238702 14.238702 39.156431-1.186558 54.581692z" p-id="1658" fill="#BEE7E9"></path>
        </svg>
    )
}

export default BackTop