import React from 'react'
import { Link } from 'react-router-dom'
import miment from 'miment'
import './Home.scss'

const Home = ({ jsonDataArr = [] }) => {
    const showData = jsonDataArr.sort((a, b) => b.time - a.time)
    return (
        <ul>
            <h1 className="top-text">近期文章</h1>
            { showData.map(item => (
                <li key={item.id} className="item-container">
                    <h3><Link to={`/blog/article/${item.id}`} className="title">{ item.title }</Link></h3>
                    <div className="extra">{ `分类：${item.extra}` }</div>
                    <div className="time">{ `创建时间：${miment(item.time).format()}` }</div>
                </li>
            )) }
        </ul>
    )
}


export default Home