const html = '\
<div  id=\'write\'  class = \'\'><h1><a name="浏览器缓存机制" class="md-header-anchor"></a><span>浏览器缓存机制</span></h1><div class=\'md-toc\' mdtype=\'toc\'><p class="md-toc-content" role="list"><span role="listitem" class="md-toc-item md-toc-h1" data-ref="n0"><a class="md-toc-inner" href="#浏览器缓存机制">浏览器缓存机制</a></span><span role="listitem" class="md-toc-item md-toc-h2" data-ref="n4"><a class="md-toc-inner" href="#强缓存">强缓存</a></span><span role="listitem" class="md-toc-item md-toc-h2" data-ref="n22"><a class="md-toc-inner" href="#协商缓存">协商缓存</a></span><span role="listitem" class="md-toc-item md-toc-h2" data-ref="n38"><a class="md-toc-inner" href="#何时使用">何时使用</a></span></p></div><h2><a name="强缓存" class="md-header-anchor"></a><span>强缓存</span></h2><ul><li><p><span>由两个字段控制：</span></p><ol><li><code>HTTP1.0</code><span>提供的 </span><code>Expires</code><span>，值为⼀个绝对时间表示缓存新鲜⽇期 </span></li><li><code>HTTP1.1</code><span>提供的 </span><code>Cache-Control: max-age=</code><span> ，值为以秒为单位的最大新鲜时间</span></li></ol></li><li><p><span>没有过期则使用缓存，已过期则重新请求</span></p></li></ul><h2><a name="协商缓存" class="md-header-anchor"></a><span>协商缓存</span></h2><ul><li><p><span>有两个字段控制：</span></p><ol><li><code>ETag</code><span>：文件的 </span><code>hash</code><span>值</span></li><li><code>Last-Modified</code><span>：文件的修改时间，精确到秒</span></li></ol></li><li><p><span>协商缓存就是强制缓存失效后，浏览器携带缓存标识向服务器发起请求，由服务器根据缓存标识决定是否使用缓存，服务器对比后，若改变则返回新资源，若没改变则返回</span><code>304</code></p></li></ul><h2><a name="何时使用" class="md-header-anchor"></a><span>何时使用</span></h2><blockquote><p><strong><span>如果有新版本出现，但资源还在强缓存时间内，用户不就看不到最新的资源了吗？</span></strong></p></blockquote><p><span>所以前端最好的缓存方案是：</span><code>html</code><span>文档使用协商缓存，其他静态资源使用强缓存</span></p><p><span>当静态文件（如图片等），发生改变时，则会改变</span><code>hash</code><span>值，这样html文档请求的静态文件路径就会改变，就可以拿到最新的静态文件了</span></p></div>\
'; export default html