import React, { useRef } from 'react'
import Dotline from './Dotline'

const Style = {
    zIndex: -1,
    position: 'fixed',
    top: 0,
    right: 0,
    bottom: 0,
    left: 0,
    backgroundSize: 'cover',
    backgroundRepeat: 'no-repeat',
    backgroundPosition: '0% 50%'
}

const Background = ({ img, clientWidth, clientHeight }) => {
    const canvasRef = useRef(null)
    if (canvasRef.current) {
        const DotlineOption = {
            dom: canvasRef.current, //画布dom
            cw: clientWidth, //画布宽
            ch: clientHeight, //画布高
            ds: 150, //点的个数
            r: 3, //圆点半径
            cl: '#8799a3', //粒子线颜色
            dis: 150 //触发连线的距离
        }
        new Dotline(DotlineOption).start();
    }
    return (
        <div style={{ background: `url(${img.default})`, ...Style }}>
            <canvas ref={canvasRef}></canvas>
        </div>
    )
}

export default Background