import './App.css';
import React, { useState, useEffect } from 'react'
import { BrowserRouter, Route } from 'react-router-dom'
import JsonData from './config/catalog.json'
import Background from './components/Background'
import Tab from './components/Tab'
import Search from './components/Search'
import BackTop from './components/BackTop'
import Home from './components/Home'
import Catalog from './components/Catalog'
import Article from './components/Article'

// 背景图片
const BackgroundImg = require('./config/background.jpg')

function App() {
  // DataToArr，把jsonData转化为Arr形式
  const [ jsonDataArr, setJsonDataArr ] = useState([])
  useEffect(() => { // 把数据整理成Array形式
    let arr = []
    Reflect.ownKeys(JsonData).forEach(tab => {
        Reflect.ownKeys(JsonData[tab]).forEach(label => {
            const result = JsonData[tab][label].map(item => ({
                id: item.id,
                title: item.title,
                extra: `${tab} —— ${label}`,
                time: item.time
            }))
            arr = [ ...arr, ...result ]
        })
    })
    setJsonDataArr(arr)
  }, [])

  // 监听屏幕宽度
  const [ isPC, setIsPC ] = useState(true)
  const [ clientWidth, setClientWidth ] = useState(1200)
  const [ clientHeight, setClientHeight ] = useState(800)
  useEffect(() => {
    const clientWidth = document.body.clientWidth || document.documentElement.clientWidth;
    const clientHeight = document.documentElement.clientHeight || document.body.clientHeight;
    setIsPC( clientWidth <= 750 ? false : true )
    setClientWidth(clientWidth)
    setClientHeight(clientHeight)
  }, [])

  // 所有标签
  const tabs = Reflect.ownKeys(JsonData)

  return (
    <div className="App">
      <Background img={BackgroundImg} clientWidth={clientWidth} clientHeight={clientHeight} />
      <BrowserRouter>
        <>
          <Tab tabs={tabs} isPC={isPC} />
          <Search jsonDataArr={jsonDataArr}/>
          <BackTop isPC={isPC} />
          <Route path='/' exact render={() => (<Home jsonDataArr={jsonDataArr}/>)}></Route>
          <Route path='/blog' exact render={() => (<Home jsonDataArr={jsonDataArr}/>)}></Route>
          <Route path="/blog/catalog/:tab" exact render={() => (<Catalog jsonData={JsonData}/>)}></Route>  
          <Route path="/blog/article/:id" exact component={Article}></Route>  
        </>
      </BrowserRouter>
    </div>
  );
}

export default App;
