const html = '\
<div  id=\'write\'  class = \'\'><h1><a name="离线缓存-manifest" class="md-header-anchor"></a><span>离线缓存 manifest</span></h1><div class=\'md-toc\' mdtype=\'toc\'><p class="md-toc-content" role="list"><span role="listitem" class="md-toc-item md-toc-h1" data-ref="n0"><a class="md-toc-inner" href="#离线缓存-manifest">离线缓存 manifest</a></span><span role="listitem" class="md-toc-item md-toc-h2" data-ref="n14"><a class="md-toc-inner" href="#使用">使用</a></span><span role="listitem" class="md-toc-item md-toc-h2" data-ref="n18"><a class="md-toc-inner" href="#工作原理">工作原理</a></span></p></div><h2><a name="使用" class="md-header-anchor"></a><span>使用</span></h2><ol><li><span>在</span><code>&lt;html&gt;</code><span>上添加属性</span><code>manifest=&quot;文件名.manifest&quot;</code></li><li><span>在 </span><code>文件名.manifest</code><span> ⽂件的编写离线存储的资源 </span></li><li><span>在离线状态时，操作 </span><code>window.applicationCache</code><span> 进⾏需求实现</span></li></ol><h2><a name="工作原理" class="md-header-anchor"></a><span>工作原理</span></h2><ol><li><p><span>在线的情况下浏览器发现 </span><code>html</code><span>头部有 </span><code>manifest</code><span>属性，它会请求 </span><code>manifest</code><span>⽂件</span></p><ul><li><span>如 果是第⼀次访问 </span><code>app</code><span>，那么浏览器就会根据</span><code>manifest</code><span>⽂件的内容下载相应的资源并且进⾏ 离线存储。</span></li><li><span>如果已经访问过 </span><code>app</code><span>并且资源已经离线存储了，那么浏览器就会使⽤离线的资 源加载⻚⾯，然后浏览器会对⽐新的 </span><code>manifest</code><span>⽂件与旧的 </span><code>manifest</code><span>⽂件，如果⽂件没 有发⽣改变，就不做任何操作，如果⽂件改变了，那么就会重新下载⽂件中的资源并进⾏离线存储</span></li></ul></li><li><p><span>离线的情况下，浏览器就直接使⽤离线存储的资源</span></p></li></ol></div>\
'; export default html