const html = '\
<div  id=\'write\'  class = \'\'><h1><a name="doctype" class="md-header-anchor"></a><span>Doctype</span></h1><div class=\'md-toc\' mdtype=\'toc\'><p class="md-toc-content" role="list"><span role="listitem" class="md-toc-item md-toc-h1" data-ref="n0"><a class="md-toc-inner" href="#doctype">Doctype</a></span><span role="listitem" class="md-toc-item md-toc-h2" data-ref="n4"><a class="md-toc-inner" href="#作用">作用</a></span><span role="listitem" class="md-toc-item md-toc-h2" data-ref="n18"><a class="md-toc-inner" href="#html5-为什么只需要写-doctype-html">HTML5 为什么只需要写 <code>&lt;!DOCTYPE HTML&gt;</code></a></span></p></div><h2><a name="作用" class="md-header-anchor"></a><span>作用</span></h2><ul><li><p><code>&lt;!DOCTYPE&gt;</code><span> 声明位于⽂档中的最前⾯，处于 </span><code>&lt;html&gt;</code><span> 标签之前。告知浏览器的解析器， ⽤什么⽂档类型 </span><code>规范</code><span>来解析这个⽂档 </span></p><ul><li><code>&lt;!DOCTYPE&gt;</code><span>不存在或格式不正确会导致⽂档以混杂模式呈现</span></li></ul></li></ul><blockquote><ol><li><span>严格模式是以该浏览器⽀持的</span><code>最⾼标准</code><span>运⾏</span></li><li><span>混杂模式是⻚⾯以宽松的</span><code>向后兼容</code><span>的⽅式显示。模拟⽼式浏览器的⾏为以防⽌站点⽆法⼯作</span></li></ol></blockquote><h2><a name="html5-为什么只需要写-doctype-html" class="md-header-anchor"></a><span>HTML5 为什么只需要写 </span><code>&lt;!DOCTYPE HTML&gt;</code></h2><p><span>1、</span><code>HTML5</code><span>不基于 </span><code>SGML</code><span>，因此不需要对 </span><code>DTD</code><span>进⾏引⽤，但是需要 </span><code>doctype</code><span>来规范浏览器 的⾏为 </span></p><p><span>2、 </span><code>HTML4.01</code><span> 基于 </span><code>SGML</code><span>,所以需要对 </span><code>DTD</code><span>进⾏引⽤，才能告知浏览器⽂档所使⽤的⽂档 类型</span></p></div>\
'; export default html