const html = '\
<div  id=\'write\'  class = \'\'><h1><a name="https" class="md-header-anchor"></a><span>HTTPS</span></h1><div class=\'md-toc\' mdtype=\'toc\'><p class="md-toc-content" role="list"><span role="listitem" class="md-toc-item md-toc-h1" data-ref="n0"><a class="md-toc-inner" href="#https">HTTPS</a></span><span role="listitem" class="md-toc-item md-toc-h2" data-ref="n7"><a class="md-toc-inner" href="#工作原理">工作原理</a></span><span role="listitem" class="md-toc-item md-toc-h2" data-ref="n24"><a class="md-toc-inner" href="#与-http-的区别">与 HTTP 的区别</a></span></p></div><h2><a name="工作原理" class="md-header-anchor"></a><span>工作原理</span></h2><ol><li><span>客户端请求服务器端简历 </span><code>SSL</code><span>链接</span></li><li><span>服务器端收到请求后，将网站证书（包含公钥）返回给客户端</span></li><li><span>双方协商加密等级</span></li><li><span>客户端根据协商一致的等级，建立密钥，通过公钥加密密钥，传给服务器端</span></li><li><span>服务器端用自己的私钥解密出密钥，并通过密钥和客户端通信</span></li></ol><h2><a name="与-http-的区别" class="md-header-anchor"></a><span>与 HTTP 的区别</span></h2><ol><li><code>http</code><span>是明文传输，</span><code>https</code><span>则是具有安全性的 </span><code>SSL</code><span>加密传输。</span></li><li><code>http</code><span>协议的端口为</span><code>80</code><span>，</span><code>https</code><span>的端口为</span><code>443</code></li><li><code>https</code><span>需要 </span><code>SSL</code><span>证书，费用更高。同时握手阶段需要更多的数据开销，时间也更久</span></li></ol></div>\
'; export default html